import React from 'react';
import ReactMarkdown from 'react-markdown';
import remarkGfm from 'remark-gfm';

const MARKDOWN_STYLES = {
  markdownParagraph: { marginBottom: '1rem' },
  markdownHeading: { fontSize: '1.5rem', fontWeight: 'bold', color: '#00BFFF', marginBottom: '1rem' },
  markdownSubheading: { fontSize: '1.25rem', fontWeight: 'bold', color: '#00BFFF', marginBottom: '1rem' },
  markdownSubsubheading: { fontSize: '1.1rem', fontWeight: 'bold', color: '#00BFFF', marginBottom: '1rem' },
  markdownList: { marginBottom: '1rem' },
  markdownNumberedList: { marginBottom: '1rem' },
  markdownListItem: { marginBottom: '0.5rem' },
  markdownQuote: { 
    borderLeft: '3px solid #00BFFF', 
    paddingLeft: '1rem', 
    color: '#B0B0B0',
    fontStyle: 'italic',
    marginBottom: '1rem'
  },
  markdownCode: { 
    backgroundColor: '#1A1A1A', 
    color: '#00BFFF', 
    padding: '2px 4px', 
    borderRadius: '3px',
    fontSize: '0.9em'
  },
  markdownCodeBlock: { 
    backgroundColor: '#1A1A1A', 
    padding: '1rem', 
    borderRadius: '5px',
    overflow: 'auto',
    fontSize: '0.9em'
  },
  markdownBold: { fontWeight: 'bold', color: '#00BFFF' },
  markdownItalic: { fontStyle: 'italic', color: '#B0B0B0' },
  markdownTable: { width: '100%', marginBottom: '1rem' },
  markdownTableRow: { borderBottom: '1px solid #333' },
  markdownTableHeader: { backgroundColor: '#121212', color: '#00BFFF', fontWeight: 'bold', padding: '0.5rem' },
  markdownTableCell: { padding: '0.5rem', color: '#B0B0B0' },
};

const Message = ({ message }) => {
  const styles = {
    message: { 
      padding: '12px', 
      marginBottom: '16px', 
      maxWidth: '90%', 
      borderRadius: '4px',
      lineHeight: '1.4',
      fontFamily: 'monospace'
    },
    messageHeader: {
      fontSize: '11px',
      marginBottom: '8px',
      fontWeight: 'bold',
      color: '#00FF00'
    },
    messageContent: {
      fontSize: '12px',
      lineHeight: '1.4'
    },
    userMessage: { 
      backgroundColor: '#000000', 
      color: '#00FF00', 
      alignSelf: 'flex-end', 
      marginLeft: 'auto', 
      border: '1px solid #00FF00',
      maxWidth: '80%'
    },
    assistantMessage: { 
      backgroundColor: '#000000', 
      color: '#00FF00', 
      alignSelf: 'flex-start', 
      marginRight: 'auto', 
      border: '1px solid #00FF00',
      maxWidth: '80%'
    },
    question: { 
      borderBottom: '1px solid #00FF00', 
      paddingBottom: '10px', 
      marginBottom: '10px',
      fontSize: '13px'
    },
    feedback: { 
      borderLeft: '3px solid #00FF00', 
      paddingLeft: '10px', 
      fontStyle: 'italic', 
      backgroundColor: '#000000',
      fontSize: '12px'
    }
  };

  return (
    <div style={{ ...styles.message, ...styles[message.type] }}>
      <div style={styles.messageHeader}>
        {message.type === 'user' ? 'You' : message.type === 'assistant' ? 'Assistant' : 'System'}
      </div>
      <div style={styles.messageContent}>
        <ReactMarkdown
          remarkPlugins={[remarkGfm]}
          className="markdown-content"
          components={{
            p: ({ children }) => <p className="markdown-paragraph" style={MARKDOWN_STYLES.markdownParagraph}>{children}</p>,
            h1: ({ children }) => <h1 className="markdown-heading" style={MARKDOWN_STYLES.markdownHeading}>{children}</h1>,
            h2: ({ children }) => <h2 className="markdown-subheading" style={MARKDOWN_STYLES.markdownSubheading}>{children}</h2>,
            h3: ({ children }) => <h3 className="markdown-subsubheading" style={MARKDOWN_STYLES.markdownSubsubheading}>{children}</h3>,
            ul: ({ children }) => <ul className="markdown-list" style={MARKDOWN_STYLES.markdownList}>{children}</ul>,
            ol: ({ children }) => <ol className="markdown-numbered-list" style={MARKDOWN_STYLES.markdownNumberedList}>{children}</ol>,
            li: ({ children }) => <li className="markdown-list-item" style={MARKDOWN_STYLES.markdownListItem}>{children}</li>,
            blockquote: ({ children }) => <blockquote className="markdown-quote" style={MARKDOWN_STYLES.markdownQuote}>{children}</blockquote>,
            code: ({ inline, className, children, ...props }) => {
              const match = /language-(\w+)/.exec(className || '');
              return !inline ? (
                <pre className="markdown-code-block" style={MARKDOWN_STYLES.markdownCodeBlock}>
                  <code className={className} {...props}>
                    {String(children).replace(/\n$/, '')}
                  </code>
                </pre>
              ) : (
                <code className={className} style={MARKDOWN_STYLES.markdownCode} {...props}>
                  {children}
                </code>
              );
            },
            strong: ({ children }) => <strong className="markdown-bold" style={MARKDOWN_STYLES.markdownBold}>{children}</strong>,
            em: ({ children }) => <em className="markdown-italic" style={MARKDOWN_STYLES.markdownItalic}>{children}</em>,
            table: ({ children }) => <table className="markdown-table" style={MARKDOWN_STYLES.markdownTable}>{children}</table>,
            tr: ({ children }) => <tr className="markdown-table-row" style={MARKDOWN_STYLES.markdownTableRow}>{children}</tr>,
            th: ({ children }) => <th className="markdown-table-header" style={MARKDOWN_STYLES.markdownTableHeader}>{children}</th>,
            td: ({ children }) => <td className="markdown-table-cell" style={MARKDOWN_STYLES.markdownTableCell}>{children}</td>,
          }}
        >
          {message.content}
        </ReactMarkdown>
      </div>
    </div>
  );
};

export default Message;
