import CryptoJS from 'crypto-js';

// Generate a secure key
const key = CryptoJS.lib.WordArray.random(32); // 256-bit key
const iv = CryptoJS.lib.WordArray.random(16); // 128-bit IV

// Securely store the key in localStorage
if (!localStorage.getItem('encryptionKey')) {
  localStorage.setItem('encryptionKey', CryptoJS.enc.Base64.stringify(key));
  localStorage.setItem('encryptionIV', CryptoJS.enc.Base64.stringify(iv));
}

// Load the key from localStorage
const storedKey = localStorage.getItem('encryptionKey');
const storedIV = localStorage.getItem('encryptionIV');

if (!storedKey || !storedIV) {
  throw new Error('Encryption key not found');
}

// Export encryption functions
export function encrypt(data) {
  try {
    const encrypted = CryptoJS.AES.encrypt(
      data,
      CryptoJS.enc.Base64.parse(storedKey),
      {
        iv: CryptoJS.enc.Base64.parse(storedIV),
        mode: CryptoJS.mode.CBC,
        padding: CryptoJS.pad.Pkcs7
      }
    );
    return encrypted.toString();
  } catch (error) {
    console.error('Encryption error:', error);
    return null;
  }
}

export function decrypt(encryptedData) {
  try {
    const decrypted = CryptoJS.AES.decrypt(
      encryptedData,
      CryptoJS.enc.Base64.parse(storedKey),
      {
        iv: CryptoJS.enc.Base64.parse(storedIV),
        mode: CryptoJS.mode.CBC,
        padding: CryptoJS.pad.Pkcs7
      }
    );
    return decrypted.toString(CryptoJS.enc.Utf8);
  } catch (error) {
    console.error('Decryption error:', error);
    return null;
  }
}

// Add data cleanup function
export function clearEncryptionData() {
  localStorage.removeItem('encryptionKey');
  localStorage.removeItem('encryptionIV');
  localStorage.removeItem('session_data');
}

// Add data validation
export function validateData(data) {
  if (!data || typeof data !== 'string') {
    return false;
  }
  try {
    // Basic validation
    const parsed = JSON.parse(data);
    if (!parsed.timestamp || !parsed.data) {
      return false;
    }
    return true;
  } catch (error) {
    return false;
  }
}

// Add session management
export function getSessionData() {
  const encrypted = localStorage.getItem('session_data');
  if (!encrypted) return null;
  
  const decrypted = decrypt(encrypted);
  if (!decrypted) return null;
  
  try {
    const data = JSON.parse(decrypted);
    const now = new Date().getTime();
    if (now - data.timestamp > 3600000) { // 1 hour
      clearEncryptionData();
      return null;
    }
    return data;
  } catch (error) {
    console.error('Session data error:', error);
    clearEncryptionData();
    return null;
  }
}
